.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header {
  width: 100%;
  height: 80px;
  background-color: yellowgreen;
  font-size: 2rem;
  line-height: 80px;
  text-align: left;
  padding-left: 40px;
}

.list {
  width: 100%;
  text-align: left;
  padding-left: 20px;
  border-bottom: 2px solid #ddd;
}

.list h4 {
  margin: 15px 0 10px;
  font-size: 1rem;
}

.list p {
  margin-bottom: 20px;
}

button {
  all: unset;
  cursor: pointer;
}

.modal {
  width: 100%;
  height: 400px;
  background-color: #ddd;
  font-size: 1rem;
  text-align: left;
  padding: 20px 0 0 20px;
  margin-top: 50px;
}

.modal p:nth-child(2) {
  margin: 60px 0;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
      animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
      transform: rotate(0deg);
  }
  to {
      transform: rotate(360deg);
  }
}
